.content--chat {
    height: 90vh;
}

.chat {
    width: 100%;
    background: $black;
    padding: 27px 50px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    margin-bottom: 30px;

    &__footer {
        margin-top: auto;
    }

    &__title {
        font-family: 'Montserrat', sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: 32px;
        letter-spacing: 0em;
        text-align: left;
        color: #fff;
        text-transform: uppercase;
        margin-bottom: 26px;
    }

    form {
        display: flex;
        width: 100%;
        margin-bottom: 20px;
    }

    &__input {
        background: #fff;
        height: 48px;
        padding-left: 20px;
        padding-top: 17px;
        border: none;
        box-shadow: none;
        width: 80%;
        border-radius: 5px 0 0 5px;

        &::placeholder {
            color: #767676;
            font-size: 16px;
            font-style: normal;
            font-weight: 600;
            letter-spacing: 0em;
            text-align: left;
        }

        &:active {
            outline: none;
        }

        &:focus {
            outline: none;
        }
    }

    &__btn {
        border-radius: 0 5px 5px 0;
        width: 20%;


    }

    &__faq {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    &__questn {
        display: flex;
        align-items: center;
    }

    &__time {
        font-family: 'Montserrat', sans-serif;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 28px;
        text-align: left;
        color: #B8B8B8;
    }

    &__txt {
        font-size: 16px;
        font-style: normal;
        font-weight: 600;
        line-height: 32px;
        text-align: left;
        color: #fff;
        margin-left: 28px;
    }

    &__answ {
        font-size: 16px;
        font-style: normal;
        font-weight: 600;
        line-height: 32px;
        text-align: left;
        padding: 8px 20px;
        background: rgba(184, 184, 184, 0.25);
        color: #fff;
        border-radius: 5px;
        margin: 20px 0;
        display: block;

    }

    &__operator {
        font-size: 14px;
        font-style: normal;
        font-weight: 600;
        line-height: 28px;
        text-align: left;
        color: #B8B8B8;
        padding: 5px 20px;
        width: 40%;
        background: rgba(184, 184, 184, 0.25);
        border-radius: 5px;
    }

    &__img {
        position: absolute;
        left: 0;
        bottom: -5px;
        width: 100%;
    }

}

.loading:after {
    content: '.';
    animation: loading 1s ease alternate infinite;
}

@-webkit-keyframes loading {
    60% {
        text-shadow: 0.35em 0 0 currentColor;
    }

    100% {
        text-shadow: 0.35em 0 0 currentColor, 0.75em 0 0 currentColor;
    }
}

@keyframes loading {
    60% {
        text-shadow: 0.35em 0 0 currentColor;
    }

    100% {
        text-shadow: 0.35em 0 0 currentColor, 0.75em 0 0 currentColor;
    }
}


textarea::-webkit-input-placeholder {
    opacity: 1;
    transition: opacity 0.3s ease;
}

textarea::-moz-placeholder {
    opacity: 1;
    transition: opacity 0.3s ease;
}

textarea:-moz-placeholder {
    opacity: 1;
    transition: opacity 0.3s ease;
}

textarea:-ms-input-placeholder {
    opacity: 1;
    transition: opacity 0.3s ease;
}

textarea:focus::-webkit-input-placeholder {
    opacity: 0;
    transition: opacity 0.3s ease;
}

textarea:focus::-moz-placeholder {
    opacity: 0;
    transition: opacity 0.3s ease;
}

textarea:focus:-moz-placeholder {
    opacity: 0;
    transition: opacity 0.3s ease;
}

textarea:focus:-ms-input-placeholder {
    opacity: 0;
    transition: opacity 0.3s ease;
}

@media only screen and (max-width : 1200px) {
    .content--chat {
        height: 80vh;
    }

    .chat {
        padding: 27px 15px;
        height: auto;

        &__faq {
            margin-bottom: 100px;
        }
    }
}

@media only screen and (max-width : 992px) {
    .chat {
        padding: 30px 40px 50px 40px;

        &__operator {
            width: 60%;
        }
    }

}

@media only screen and (max-width : 681px) {
    .content--chat {
        height: 80vh;
    }

    .chat {
        padding: 27px 15px;
        height: auto;

        &__operator {
            width: 100%;
        }

        form {
            flex-wrap: wrap;
        }

        &__input {
            height: 86px;
            width: 100%;
            border-radius: 5px 5px 5px 5px;
            margin-bottom: 10px;

        }

        &__btn {
            border-radius: 5px 5px 5px 5px;
            width: 100%;


        }

    }

}
