.excharge {
    margin-bottom: 30px;

    &__form {
        display: flex;
    }

    &__input {
        height: 40px;
        border: 1px solid #B8B8B8;
        box-sizing: border-box;
        border-radius: 5px 0px 0px 5px;
        padding-left: 20px;
        padding-right: 90px;
        font-size: 12px;
        font-weight: 700;
        color: #B8B8B8;

        &::placeholder {
            color: #B8B8B8;
        }
    }

    &__btn {
        border-radius: 0px 5px 5px 0px;
        background: $black;
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 90px;
        font-weight: 700;
        text-transform: uppercase;
        font-size: 12px;
        cursor: pointer;

        &:hover {
            background: lighten($black, 20%);
        }
    }
}
