.content-image {
    position: fixed;
    bottom: 0;
    margin-top: auto;
    width: 100%;
    left: 374px;
    z-index: -1;

    img {
        display: block;
    }

}

.content {
    padding: 60px;
    box-sizing: border-box;

}

.block-title {
    font-family: 'Raleway', sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 32px;
    letter-spacing: 0em;
    text-align: left;
    color: $black;
    margin-bottom: 20px;

    span {
        color: #B8B8B8;
    }

}

@media only screen and (max-width : 1200px) {
    .content-image {
        width: 100%;
        left: 0px;

    }
}

@media only screen and (max-width : 992px) {
    .content {
        padding: 40px;
    }


}

@media only screen and (max-width : 576px) {
    .content {
        padding: 15px;
    }


}
