.blackpart {
    width: 100%;
    background: $black;
    border-radius: 5px;
    position: relative;
    display: flex;
    justify-content: center;
    padding: 27px 15px 27px 15px;
    margin-bottom: 25px;

    &__title {
        font-family: 'Montserrat', sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: 32px;
        letter-spacing: 0em;
        text-align: center;
        color: #fff;
        text-transform: uppercase;
        margin-bottom: 26px;

    }

}

form {
    z-index: 2;
    /*    display: flex;
    flex-direction: column;
    justify-content: center;*/
    width: 438px;
}

.blackpart-imgs {
    display: flex;
    justify-content: space-between;
    position: absolute;
    bottom: 0;
    width: 100%;
    z-index: 1;
}

.whitefields__input {
    width: 100%;
    height: 48px;
    background: #fff;
    box-shadow: none;
    border: none;
    border-radius: 5px;
    margin-bottom: 10px;
    display: block;
    padding: 0 15px;
    font-size: 16px;
    font-weight: 600;

}

.text-right {
    text-align: right;
}

.input-summ {
    display: flex;
    position: relative;
    height: 48px;
    margin-bottom: 10px;

    span {
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
        font-family: 'Raleway', sans-serif;
        font-size: 16px;
        font-style: normal;
        font-weight: 600;
        letter-spacing: 0em;
        text-align: left;
        background: #fff;
        display: flex;
        align-items: center;
        color: #767676;
        border-right: 1px solid $black;
        width: 50%;
        padding-left: 20px;
        border-radius: 5px 0 0 5px;

    }
}

.ic_finance {
    width: 18px;
    height: 18px;
    stroke: $yellow;
    fill: $black;
    margin-left: 10px;
}

.btn-dark {
    font-family: Raleway;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 32px;
    letter-spacing: 0em;
    text-align: center;
    padding: 8px 0;
    width: 100%;
    border: 1px solid #fff;
    border-radius: 5px;
    margin-bottom: 10px;
    transition: all .3s;

    &:hover {
        border: 1px solid $yellow;
        background: $yellow;
        color: $black;

    }

    &:hover .ic_finance {
        width: 18px;
        height: 18px;
        stroke: $black;
        fill: $yellow;
        margin-left: 10px;
    }


}


@media only screen and (max-width : 1200px) {
    .bt-finance {
        order: 3;
        flex-basis: 100%;
        margin-bottom: 0;
        margin-top: 30px;
    }

    .blackpart-imgs {
        display: none;
    }
}
