.visible {
    display: block;

}

.close_aside {
    display: none;
    position: absolute;
    top: 15px;
    right: 15px;
}

.header {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__img {
        width: 103px;
        height: 103px;
        border-radius: 50%;
        border: 2px solid #fff;
        overflow: hidden;
        text-align: center;
        margin-bottom: 10px;


        img {
            height: 100%;
            object-fit: cover;
        }
    }

    &__name {
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: 32px;
        letter-spacing: 0em;
        text-align: center;
        color: #fff;

    }

    &__email {
        font-size: 15px;
        font-style: normal;
        font-weight: 500;
        line-height: 30px;
        letter-spacing: 0em;
        text-align: center;
        color: #fff;
        margin-bottom: 55px;

        &:hover {
            color: $yellow
        }
    }
}

.navigation {
    display: flex;
    flex-direction: column;
    align-items: flex-start;

    &__ic {
        width: 20px;
        height: 20px;
        fill: #B8B8B8;
        margin-right: 20px;
    }

    li {
        margin: 15px 0;
        position: relative;


        a {
            font-size: 16px;
            font-style: normal;
            font-weight: 700;
            line-height: 32px;
            letter-spacing: 0em;
            text-align: left;
            display: flex;
            align-items: center;
            color: #fff;

            &:hover {
                color: $yellow;

            }
        }
    }

    .active:before {
        content: '';
        position: absolute;
        width: 400px;
        height: 50px;
        background: #4F4F4F;
        border-radius: 0px 10px 10px 0px;
        display: block;
        z-index: -1;
        top: 0;
        bottom: 0;
        margin: auto auto auto -150px;

    }

}

.active .navigation__ic {
    fill: $yellow;

}

.footer {
    padding-bottom: 100px;
    text-align: start;

    img {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 98%;
        z-index: -1;
    }

    a {
        font-size: 16px;
        font-style: normal;
        font-weight: 700;
        line-height: 32px;
        letter-spacing: 0em;
        text-align: left;
        display: flex;
        align-items: center;
        color: #fff;

        &:hover {
            color: $yellow;

        }
    }


}

@media only screen and (max-width : 1200px) {
    .close_aside {
        display: block;
    }
}

@media only screen and (max-width : 576px) {
    .header {
        &__email {
            margin-bottom: 10px;
        }
    }

    .navigation {
        padding-left: 40px;
        margin-bottom: 20px;

        li {
            margin: 10px 0;

            a {
                font-size: 16px;
            }
        }

    }

    .footer {
        padding-left: 40px;
    }
}
