.buttons {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 30px;
    justify-content: space-between;

    &__block {
        flex-basis: 18%;
        display: flex;
        flex-direction: column;
        margin-bottom: 30px;


    }
}

.ic_btn {
    width: 20px;
    height: 20px;
    fill: #fff;
    margin-left: 10px;
}

@media only screen and (max-width : 1552px) {
    .buttons {

        &__block {
            flex-basis: 32%;
        }
    }
}

@media only screen and (max-width : 657px) {
    .buttons {
        &__block {
            flex-basis: 48%;
        }
    }
}
