.fintabs {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.fintab-sm {
    display: inline-block;
    width: 418px;
    margin-bottom: 30px;
}

.fintab-lg {
    width: 795px;
}

.ic_dwn {
    width: 15px;
    height: 15px;
}



.document {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

@media only screen and (max-width : 1372px) {
    .fintabs {
        display: block;

    }



}

@media only screen and (max-width : 576px) {
    .fintab-sm {
        width: 100%;
    }

}
