table-responsive {
    box-sizing: border-box;
}

.table-long {
    width: 1395px;
}

.table > :not(caption) > * > * {
    background-color: #ffffff;


}

th {
    border: none;
}

thead {
    tr {
        border: none;

    }

    th {
        font-family: Raleway;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 28px;
        letter-spacing: 0em;
        text-align: left;
        text-transform: uppercase;


    }
}

tbody td {
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 28px;
    letter-spacing: 0em;
    text-align: left;
    vertical-align: middle;

    span {
        img {
            width: 25px;
            height: auto;
        }
    }
}



.table-region {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.stat-table {
    margin-top: 30px;
}
