.btn-transparent {
    padding: 4px 0;
    background: transparent;
    border: 1px solid #B8B8B8;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    line-height: 24px;
    text-transform: uppercase;
    color: #B8B8B8;
    font-weight: 700;

    &:hover {
        color: $black;
        border: 1px solid $black;

    }

}

.btn-transparent--active {
    color: $black;
    border: 1px solid $black;
}



@media only screen and (max-width : 576px) {
    .btn-transparent {
        font-size: 10px;

    }
}


.btn-black {
    font-family: "Raleway", sans-serif;
    background: $black;
    color: #fff;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    box-shadow: none;
    text-transform: uppercase;
    font-size: 12px;
    line-height: 24px;
    padding: 8px 70px;
    font-weight: 700;
    cursor: pointer;


    &:hover {
        background: lighten($black, 20%);
    }

}

.btn-block {
    font-family: "Raleway", sans-serif;
    background: #B8B8B8;
    color: #fff;
    font-weight: 700;
    line-height: 24px;
    font-size: 12px;
    text-transform: uppercase;
    padding: 9px 30px;
    border: none;
    box-shadow: none;
    border-radius: 5px;
    cursor: pointer;

    &:hover {
        background: darken(#B8B8B8, 20%);
    }


}

.btn-block--active {
    background: $yellow;
    cursor: default;

    &:hover {
        background: $yellow;

    }
}

.btn-custom {
    font-family: 'Raleway', sans-serif;
    border-radius: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    font-size: 16px;
    font-style: normal;
    line-height: 21px;
    letter-spacing: 0.03em;
    color: #fff;
    margin-bottom: 10px;
    padding: 18px 0px;
}

@media only screen and (max-width : 576px) {
    .btn-custom {
        font-size: 10px;
        padding: 14px 0px;
    }
}

.btn-custom--green {
    background: linear-gradient(283.7deg, #88B003 -19.51%, #BCCC27 107.52%);
}

.btn-custom--blue {
    background: linear-gradient(286.16deg, #12BCD2 5.83%, #DCF3FF 117.26%), linear-gradient(283.7deg, #88B003 -19.51%, #BCCC27 107.52%);
}

.btn-custom--orange {
    background: linear-gradient(104.87deg, #F96957 28.29%, #FCD263 93.86%);
}

.btn-custom--salat {
    background: linear-gradient(287.28deg, #80F8B8 19.47%, #38CDC5 96.71%);
}

.btn-custom--fiolett {
    background: linear-gradient(283.73deg, #A58CF0 26.23%, #EBD8FF 97.27%);
}

.btn-yellow {
    background: $yellow;
    border-radius: 5px;
    font-family: Raleway;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 32px;
    letter-spacing: 0em;
    text-align: center;
    padding: 8px 0;
    width: 100%;
    border: none;
    margin-bottom: 10px;

    &:hover {
        background: lighten($yellow, 10%)
    }

}
