.blackboxes {
    display: flex;
    justify-content: flex-start;
    margin-bottom: 30px;
}

.blackbox-wrp {
    text-align: center;

    .filters-check {
        justify-content: center;
    }
}

.blackbox {
    width: 245px;
    height: 243px;
    background: #202020;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-right: 30px;
    margin-bottom: 25px;

    &:last-child {
        margin-right: 0;
    }

    &__title {
        font-size: 16px;
        line-height: 32px;
        font-weight: 800;
        color: #fff;
        margin-bottom: 26px;
        text-align: center;
    }

    &__img {
        width: 37px;
        height: 27px;
    }

    a {
        transition: all .3s;
    }

    a:hover {
        opacity: .7;
    }
}

@media only screen and (max-width : 610px) {
    .blackboxes {
        display: flex;
        justify-content: flex-start;
        margin-bottom: 30px;
        flex-wrap: wrap;
    }

    .blackbox-wrp {
        margin-bottom: 30px;
        flex-basis: 100%;
    }

    .blackbox {
        flex-basis: 100%;
        width: 100%;
        height: 243px;
        padding: 20px;

    }


}
