.hamburger {
    display: none;

}

@media only screen and (max-width : 1200px) {
    .hamburger {
        display: flex;
        flex-direction: column;
        height: 30px;
        width: 50px;
        justify-content: space-between;
        order: 1;

        .line {
            width: 100%;
            display: block;
            height: 2px;
            background: $black;
        }
    }
}

@media only screen and (max-width : 576px) {
    .hamburger {
        height: 20px;
        width: 30px;
    }
}
