.date-wrp {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.date {
    flex-basis: 30%;
    margin-bottom: 20px;

}

.date-btns {
    flex-basis: 70%;
}

.input_wrp {
    position: relative;
}

.date-period {
    border: 1px solid #A3A4A6;
    border-radius: 5px;
    font-family: 'Montserrat', sans-serif;
    font-size: 15px;
    font-style: normal;
    font-weight: 600;
    line-height: 30px;
    letter-spacing: 0em;
    text-align: left;
    padding: 9px 22px 9px 53px;
    position: relative;

}

.ic_date {
    width: 20px;
    height: 18px;
    fill: $yellow;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    left: 9px;


}

.date-btns {
    display: flex;
    justify-content: space-between;


}

.date-btns__btn {
    padding: 5px 20px;
    font-family: Montserrat;
    font-size: 12px;
    font-style: normal;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: center;
    border: 1px solid #A3A4A6;
    border-radius: 5px;
    text-transform: uppercase;
    background: #fff;
    color: #A3A4A6;
    transition: all .3s;
    margin-right: 15px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;

    &:last-child {
        margin-right: 0;
    }

    &:hover {
        border: 1px solid $black;
        color: $black;

    }
}

.date-btns--active {
    border: 1px solid $black;
    color: $black;

}

@media only screen and (max-width : 1587px) {
    .date {
        flex-basis: 100%;
    }

    .date-btns {
        flex-basis: 100%;
    }

}

@media only screen and (max-width : 1067px) {
    .date-btns {
        overflow-x: auto;
        flex-wrap: nowrap;
    }

    .date-btns__btn {
        flex: 0 0 auto;
    }

}
