.code {
    margin-bottom: 30px;
}

.design {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;


    &__block {
        flex-basis: 18%;
        margin-right: 0px;
        margin-bottom: 30px;


        &:last-child {
            margin-right: 0;
        }
    }

    &__img {
        /*        width: 245px;
        height: 160px;*/
        border-radius: 5px;
        overflow: hidden;
        margin-bottom: 10px;

        img {
            height: 100%;
        }
    }

    .btn-block {
        width: 100%;
    }
}



@media only screen and (max-width : 1552px) {
    .design {


        &__block {

            margin-right: 0px;
            flex-basis: 32%;

        }

        &__img {
            width: 100%;
            height: auto;
        }

    }

}

@media only screen and (max-width : 657px) {
    .design {
        display: flex;
        justify-content: space-between;

        &__block {
            margin-right: 0px;
            flex-basis: 48%;

        }



    }

}
