.custom-table {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.table-row {
    display: flex;

}

.gray {
    background: #F9F9F9;
}


.table-col {
    width: 170px;
    border: 1px solid #d4d4d4;
    margin-top: -1px;
    margin-left: -1px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background: #fff;
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 28px;
    letter-spacing: 0em;
    text-align: left;
    padding: 0 15px;


}

.w-300 {
    width: 300px;
    text-align: center;
}




.w-170 {
    width: 170px;
    font-size: 18px;
    font-weight: 700;
    font-family: 'Montserrat', sans-serif;
}

.w-81 {
    width: 81px;
    height: 50px;
    background: $yellow;
    font-size: 18px;
    font-weight: 700;
    font-family: 'Montserrat', sans-serif;

}

.table-head {
    border: none;
    background: #fff;
    text-transform: uppercase;
    font-size: 14px;
}


.no-borders {
    border-top: none;
    border-bottom: none;
}

.no-border-b {
    border-bottom: none;
}

.no-border-t {
    border-top: none;
}

.table_flag {
    position: absolute;
}

.save_settings {
    margin: 20px 0;

}





@media only screen and (max-width : 576px) {
    .excharge {
        display: none;
    }

    .tables {
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        overflow-x: auto;
    }

    .custom-table {
        width: 700px;
        flex: 0 0 auto;
    }

    .table-col {

        margin-top: -1px;
        margin-left: 0;
        margin-right: -1px;

    }

    .save_settings {
        margin: 20px 0;

        .btn-black {
            width: 100%;
        }
    }
}
