.top-part {
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;

    &__btns {
        display: flex;
        flex-wrap: wrap;

        .btn-transparent {
            margin-right: 20px;
            width: 210px;
            margin-bottom: 20px;

            &::last-child {
                margin-right: 0;
            }
        }
    }

    &__logo {
        max-width: 70px;

        img {
            width: 100%;
            object-fit: cover;
        }
    }


}

.tppage {
    justify-content: flex-end !important;
}

@media only screen and (max-width : 1200px) {
    .tppage {
        justify-content: space-between !important;
    }

    .top-part {
        flex-wrap: wrap;

        .top-part__btns {
            margin-top: 42px;
            flex-basis: 100%;
            order: 3;
        }

        .hamburger {

            order: 1;
        }

        &__logo {
            order: 2;

        }

    }
}


@media only screen and (max-width : 768px) {
    .top-part {
        margin-bottom: 40px;

        &__btns {

            .btn-transparent {
                padding: 3px 0;
                width: 216px;
                line-height: 24px;

                &:nth-child(3) {
                    margin-right: 0;
                }
            }
        }
    }
}

@media only screen and (max-width : 767px) {
    .top-part {
        &__btns {
            .btn-transparent {
                &:nth-child(3) {
                    margin-right: 15px;
                }
            }
        }
    }
}


@media only screen and (max-width : 576px) {
    .top-part {
        margin-bottom: 15px;

        &__btns {
            order: 3;
            display: flex;
            flex-wrap: nowrap;
            overflow-x: auto;

            .btn-transparent {
                flex: 0 0 auto;
                margin-right: 5px;
                width: 165px;

                &::last-child {
                    margin-right: 0;
                }
            }
        }
    }
}
