.file-upload input[type="file"] {
    display: none;
}


/* задаем стили кнопки выбора файла*/

.file-upload {
    position: relative;
    overflow: hidden;
    width: 275px;
    height: 48px;
    background: #fff;
    border-radius: 5px;
    color: $black;
    text-align: center;
    border: 1px solid #A3A4A6;
    margin-bottom: 30px;
    transition: all .3s;

}

.file-upload:hover {
    /*    background: #f8f8f8;*/
    border: 1px solid $black;

}

.file-upload:hover .btn_text {
    border-right: 1px solid $black;
}

.file-upload label {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;

}

.file-wrp {
    display: flex;
    justify-content: space-between;
    height: 48px;
}

.btn_text {
    transition: all .3s;
    border-right: 1px solid #A3A4A6;
    flex-basis: 80%;
    font-size: 12px;
    font-style: normal;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: 0em;
    text-align: left;
    color: #B8B8B8;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 20px;
}

.btn_icon {
    flex-basis: 20%;
    display: flex;
    justify-content: center;
    align-items: center;

}

.ic_file {
    width: 32px;
    height: 32px;

}

.btn__succsess {
    color: green;
    display: none;
}

.file-upload--big {
    width: 500px;

    .btn_text {
        flex-basis: 85%;
    }

    .btn_icon {
        flex-basis: 15%;
    }
}

.file-upload--xxl {
    width: 550px;

    .btn_text {
        flex-basis: 90%;
    }

    .btn_icon {
        flex-basis: 10%;
    }
}

@media only screen and (max-width : 576px) {

    .file-upload,
    .file-upload--big,
    .file-upload--xxl {
        width: 100%;
    }
}
